import React, { FC } from 'react';
import moment from 'moment';
import { navigate } from 'gatsby';
import { Box, PseudoBox, Text, Stack, Flex } from '@chakra-ui/core';
import { Calendar, DollarSign, Tag, UserPlus } from 'react-feather';

interface CardProps {
  pageLink: string;
  title: string;
  fundingBody: string;
  fundingAmount: string;
  principalInvestigator: string;
  projectStart: string;
  projectEnd: string;
}

export const Card: FC<CardProps> = ({
  pageLink,
  title,
  fundingBody,
  fundingAmount,
  principalInvestigator,
  projectStart,
  projectEnd,
}) => {
  const iconSize = '24px';

  const fields = [
    { icon: <Tag size={iconSize} />, component: <Text>{fundingBody}</Text> },
    {
      icon: <DollarSign size={iconSize} />,
      component: <Text>{fundingAmount}</Text>,
    },
    {
      icon: <UserPlus size={iconSize} />,
      component: <Text>{principalInvestigator}</Text>,
    },
    {
      icon: <Calendar size={iconSize} />,
      component: (
        <Box>
          <Text display={'inline-block'} fontWeight={'semibold'}>
            {moment(projectStart).format('MMMM YYYY')}
          </Text>
          <Text display={'inline-block'} mx={1}>
            till
          </Text>
          <Text display={'inline-block'} fontWeight={'semibold'}>
            {moment(projectEnd).format('MMMM YYYY')}
          </Text>
        </Box>
      ),
    },
  ];

  return (
    <PseudoBox
      p={8}
      borderWidth={1}
      borderColor={'jet-black.300'}
      borderRadius={'1rem'}
      boxShadow={
        '0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06)'
      }
      color={'jet-black.500'}
      cursor={'pointer'}
      _hover={{ backgroundColor: 'jet-black.100' }}
      onClick={() => navigate(pageLink)}
    >
      <Text fontSize={'2xl'} fontWeight={'semibold'} lineHeight={'1.75rem'}>
        {title}
      </Text>

      <Stack spacing={4} mt={4} mx={4}>
        {fields.map(({ icon, component }, index) => (
          <Flex key={index} alignItems={'center'}>
            <Box color={'emerald-green.500'} mr={[4, 6]}>
              {icon}
            </Box>
            <Box>{component}</Box>
          </Flex>
        ))}
      </Stack>
    </PseudoBox>
  );
};
